import { mapMutations, mapGetters } from 'vuex';
export default {
	components: {},
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			close: this.Static+'home/extendManageList/publishClose.png',
			customTime:{ // 自定义时间
				show: false, // 默认不显示
				selectTime: false // 默认没有选择时间
			},
			date: currentDate,
			trendKanBanText: {
				// 数据看板数据展示text
				kanBanText: '近七日',
				trendLeftText: '点击数',
				trendRightText: '总消耗',
				kanBanTime: 7, // 数据看板code
				trendLeft: '1', // 数据趋势left
				trendRight: '3' // 数据趋势right
			},
			dataTime: {
				starTime: null,
				endTime: null
			}
		};
	},
	computed: {
		i18n () {
		  return this.$t('extendManageList')  
		},
		startDate () {
			return this.getDate('start');
		},
		endDate () {
			return this.getDate('currentDate');
		},
		...mapGetters(['GET_ALITTLE'])
	},
	created() {
	},
	mounted() {},
	methods: {
		...mapMutations({
			setCustomTime: 'setCustomTime' // // 自定义时间
		}),
		clickClose() {
			console.log('触发关闭按钮');
			this.customTime.show = false; // 显示默认时间组件
			this.$store.commit('setCustomTime', this.customTime); // 更新vuex
		},
		clickPicker() {
			console.log('3')
		},
		// 选择时间
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		getDateJsh(e) {
			let day = new Date();
			day.setTime(day.getTime() - e * 24 * 60 * 60 * 1000);
			let getMonth = '';
			if (Number(day.getMonth() + 1) < 10) {
				getMonth = '0' + String(day.getMonth() + 1);
			} else {
				getMonth = String(day.getMonth() + 1);
			}
			let getDate = '';
			if (Number(day.getDate()) < 10) {
				getDate = '0' + String(day.getDate());
			} else {
				getDate = String(day.getDate());
			}
			return day.getFullYear() + '-' + getMonth + '-' + getDate;
		},
		startDateChange: function(e) {
			console.log('开始时间', e.target.value );
			let starTime = e.target.value;
			let qiTime = new Date(starTime.replace(/-/g, '/'));
			let jeTime = new Date(this.getDateJsh(0).replace(/-/g, '/'));
			if (qiTime >= jeTime) {
				uni.showToast({
					title: '起始时间应小于今天',
					icon: 'none',
					duration: 800
				});
				return;
			}
			this.dataTime.starTime = starTime;
			uni.showToast({
				title: '请输入结束时间',
				icon: 'none',
				duration: 800
			});
		},
		endDateChange: function(e) {
			console.log('结束时间', e.target.value );
			if(!this.dataTime.starTime){
				uni.showToast({
					title: '请先选择开始时间',
					icon: 'none',
					duration: 800
				});
				return false;
			};
			let endTime = e.target.value;
			let qiTime = new Date(this.dataTime.starTime.replace(/-/g, '/'));
			let jeTime = new Date(endTime.replace(/-/g, '/'));
			if (jeTime <= qiTime) {
				uni.showToast({
					title: '结束时间应大于起始时间',
					icon: 'none',
					duration: 800
				});
				return;
			};
			this.dataTime.endTime = endTime;
		},
		clickKeep () {
			console.log('触发保存按钮');
			if(!this.dataTime.starTime){
				uni.showToast({
					title: '请选择开始时间',
					icon: 'none',
					duration: 800
				});
				return false;
			}else if(!this.dataTime.endTime){
				uni.showToast({
					title: '请选择结束时间',
					icon: 'none',
					duration: 800
				});
				return false;
			}else{
				this.customTime.show = false; // 显示默认时间组件
				this.$store.commit('setCustomTime', this.customTime); 
				this.$emit('stomTime',[this.dataTime.starTime,this.dataTime.endTime])
			}
		}
	}
};
